body {
  margin: 0;
  font-family: 'Prata', serif;
}

:root {
  --cor-header: #6278F7;
  --cor-form-bg: #f7f7f7;
  --cor-form-text: #212121;
  --cor-second-text: #FFFFFF;
  --cor-hover-btn: #95FFD4;
  --cor-option: #ccc;
}

/* *Estilos de TextField y OptionsList* */
.global-field {
    margin: 24px 0;
    font-family: 'Montserrat', sans-serif;
}
.global-field label {
    font-family: 'Montserrat', sans-serif;
    font-size: 18px;
    font-weight: 600;
    display: block;
    margin-bottom: 8px;
}
.global-field select,
.global-field input {
    width: 100%;
    background-color: var(--cor-second-text);
    box-shadow: 10px 10px 30px rgba(0, 0, 0, 0.08);
    border: none;
    padding: 30px 20px;
    outline-color: var( --cor-header);
    font-size: 16px;
    box-sizing: border-box;
    appearance: none;
    padding-right: 40px; 
    cursor: pointer;
}